@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600&display=swap");

.mobile-nav-bar {
  background: linear-gradient(
      90deg,
      rgba(9, 55, 121, 1) 0%,
      rgba(9, 55, 121, 1) 0.1%,
      rgba(9, 55, 121, 1) 100%
    ),
    url("/public/assets/images/concrete-bw.png");
  background-repeat: repeat;
  z-index: 1;
  width: 100vw;
  height: 0%;
  position: fixed;
  left: 0;
  top: 0;
  visibility: hidden;
  transition: visibility 0.5s ease-in-out, height 0.65s ease-in-out;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.mobile-nav-bar.open {
  visibility: visible;
  height: 100vh;
}

.mobile-nav-bar a {
  font-size: 1rem;
  color: rgb(245 245 245);
  text-decoration: none;
  font-family: "Abel", sans-serif;
  line-height: 30px;
}
